import React from 'react';
import Config from '../config';
import { connect } from 'react-redux';
import toggleUserModal from '../redux/actions/toggleUserModal';
import { InputGroup, InputGroupAddon, Input, Modal, ModalBody, ModalFooter, ModalHeader, InputGroupText, Alert } from 'reactstrap';


class UserInfoModal extends React.Component{

    constructor(props){
        super(props);
        this._toggle = this._toggle.bind(this);
        this._buy = this._buy.bind(this);
        this.state = {
            nombre: '',
            nombre_valid: false,
            direccion: '',
            direccion_valid: false,
            documento: '',
            documento_valid: false,
            doctype: '',
            doctype_valid: false
        }
    }

    _toggle() {
        this.props.toggleUserModal(!this.props.isOpen);
        this.setState({
            nombre: '',
            nombre_valid: false,
            direccion: '',
            direccion_valid: false,
            documento: '',
            documento_valid: false,
            doctype: '',
            doctype_valid: false
        })
    }

    _buy(){
        let data = {
            userInfo:{
                    address_billing: this.state.direccion,
                    name_billing: this.state.nombre,
                    type_doc_billing: this.state.doctype,
                    number_doc_billing: this.state.documento
            },
            cart: this.props.cart
        }
        fetch(`${Config.host}/api/comprar`, {
            method: 'POST',
            body: JSON.stringify(data)
        })
        .then(response => response.json())
        .then(json => {
            if (json.code === 1){
                this._toggle();
                let handler = window.ePayco.checkout.configure({
                    key: '8e0ff42b93678dec5d425305def97f1b',
                    test: true
                })
                /*let data = {
                    ...json.data,
                    external: window.screen.width >= 500 ? 'false':'true'
                }*/
                handler.open(json.data);
                console.log(json.data);
            }
        })
    }

    render(){
        return(
            <div>
                <Modal isOpen={this.props.isOpen} toggle={this._toggle}>
                    <ModalHeader toggle={this._toggle}>
                        Formulario de envio
                    </ModalHeader>
                    <ModalBody>
                        <Alert color='warning'>
                            Asegurese de llenar el formulario con datos reales, asi podra recibir sus productos de manera efectiva
                        </Alert>
                        <InputGroup>
                        <InputGroupAddon addonType='prepend'>
                                <InputGroupText>
                                    <span style={{color: this.state.direccion_valid ? 'green':'red'}}>Direccion</span>
                                </InputGroupText>
                            </InputGroupAddon>
                            <Input placeholder='Direccion de entrega' onChange={(e) => {
                                this.setState({direccion: e.target.value});
                                if(e.target.value.length > 8){
                                    this.setState({direccion_valid: true});
                                } else {
                                    this.setState({direccion_valid: false});
                                }
                            }}/>
                        </InputGroup>
                        <br/>
                        <InputGroup>
                            <InputGroupAddon addonType='prepend'>
                                <InputGroupText>
                                    <span style={{color: this.state.nombre_valid ? 'green':'red'}}>Nombre</span>
                                </InputGroupText>
                            </InputGroupAddon>
                            <Input placeholder='Nombre Comprador' onChange={(e) => {
                                this.setState({nombre: e.target.value})
                                if(e.target.value.length >= 4){
                                    this.setState({nombre_valid: true});
                                } else {
                                    this.setState({nombre_valid: false});
                                }
                            }}/>
                        </InputGroup>
                        <br/>
                        <InputGroup>
                            <InputGroupAddon addonType='prepend'>
                                <InputGroupText>
                                    <span style={{color: this.state.doctype_valid ? 'green':'red'}}>Tipo Documento</span>
                                </InputGroupText>
                            </InputGroupAddon>
                            <select onChange={(e) => {
                                this.setState({doctype: e.target.value})
                                if(e.target.value !== 'NA'){
                                    this.setState({doctype_valid: true});
                                } else {
                                    this.setState({doctype_valid: false});
                                }
                            }}>
                                <option value='NA'>Tipo de Documento</option>
                                <option value="CC">CC</option>
                                <option value="TI">TI</option>
                                <option value="CE">CE</option>
                            </select>
                        </InputGroup>
                        <br/>
                        <InputGroup>
                            <InputGroupAddon addonType='prepend'>
                                <InputGroupText>
                                    <span style={{color: this.state.documento_valid ? 'green':'red'}}>Documento</span>
                                </InputGroupText>
                            </InputGroupAddon>
                            <Input placeholder='Documento Comprador' onChange={(e) => {
                                this.setState({documento: e.target.value})
                                if(e.target.value.length >= 6){
                                    this.setState({documento_valid: true});
                                } else {
                                    this.setState({documento_valid: false});
                                }
                            }}/>
                        </InputGroup>
                    </ModalBody>
                    <ModalFooter>
                        {this.state.nombre_valid && this.state.direccion_valid &&
                         this.state.documento_valid && this.state.doctype_valid ? 
                         null:<span style={{color:'red'}}>Debe llenar todos los datos de forma correcta</span>
                        }
                        <button 
                            className='btn btn-success' 
                            onClick={() => this._buy()}
                            disabled={this.state.nombre_valid && this.state.direccion_valid &&
                                        this.state.documento_valid && this.state.doctype_valid ? '':'disabled'
                                     }
                        >
                                Enviar
                        </button>
                    </ModalFooter>
                </Modal>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        isOpen: state.userInfoModal.isOpen,
        cart: state.cart.cart
    }
}

const mapDispatchToProps = {
    toggleUserModal
}

export default connect(mapStateToProps, mapDispatchToProps)(UserInfoModal);